import React from 'react';

const Article = ({title,author,text}) => {

    const styles = {
        marginTop: 40
    }
    return (
        <article style={styles}>
            <h3 style={{
                marginBottom: 3,
                fontWeight: "bolder",
                textTransform: "uppercase"
            }}>{title}</h3>
            <span style={{
                display: "block",
                maringBottom: 10,
                fontSize: 12
            }}>{author}</span>
            <p styles={{
                fontSize: 15
            }}>{text}</p>
        </article>
     );
}

export default Article;